.center {
    text-align: center;
}

.hidden {
    display: none;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    margin-top: 50px;
}


.heading {
    font-size: 50px;
    font-weight: 800;
}

.user-todo {
    display: flex;
    flex-direction: column;
    gap: 10px;
}


#label-todo {
    font-size: 19px;
    font-weight: 600;
}

#todo {
    padding: 5px 10px 5px 10px;
    width: 15rem;
    outline: none;
    border: none;
    border-radius: 3px;
    font-weight: 800;
}

#submit {
    padding: 8px 15px 8px 15px;
    font-weight: 800;
    font-size: 18px;
    color: black;
    background: orange;
    border: none;
}


.todo-container {
    margin-top: 50px;
}

.items-heading {
    font-size: 40px;
    font-weight: 800;
    text-align: center;
}


.allTodos-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.box {
    background-color: white;
    color: black;
    width: 25rem;
    padding: 6px 5px 6px 7px;
    font-weight: 800;
    font-size: 20px;
    display: flex;
    align-items: center;
}
.title {
    display: flex;
    width: fit-content;
}
.delete-btn {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    margin-right: 10px;
}

#Delete {
    background-color: red;
    border: none;
    padding: 5px 10px 5px 10px;
    color: white;
    font-weight: 800;
    border-radius: 4px;
}


@media (max-width: 450px) and (min-width: 0px) {
    .box {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-right: 10px;
        margin-left: 10px;
        padding: 6px 0px 6px 0px;
    }

    .title {
        text-align: center;
    }
}


.error-page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 70px;
}

.text-1 {
    font-size: 80px;
    font-weight: 700;
}

.text-2{
    font-size: 40px;
    font-weight: 400;
    margin-left: 5px;
}